/* font-family: 'Roboto Condensed';
======================================== */
@font-face {
	font-family: 'Roboto Condensed';
	src: url('./roboto_condensed-bold/robotocondensed-bold-webfont.woff') format('woff'),
	url('./roboto_condensed-bold/robotocondensed-bold-webfont.woff2') format('woff'),
	url('./roboto_condensed-bold/RobotoCondensed-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

/* font-family: 'Montserrat';
======================================== */
@font-face {
	font-family: 'Montserrat';
	src: url('./montserrat-medium/montserrat-medium-webfont.woff') format('woff'),
	url('./montserrat-medium/montserrat-medium-webfont.woff2') format('woff'),
	url('./montserrat-medium/Montserrat-Medium.ttf') format('truetype');
	font-weight: 500;
	font-style: normal;
}

